import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";

const mode = 'all';

const defaultValues = {
    initialContribution: '',
    period: '',
    monthlyContribution: '',
    profitability: '',
    ipca: '',
    cdi: ''
};

const simulationSchemaValidation = {
    mode,
    defaultValues,
    resolver: yupResolver(
        yup.object().shape({
            initialContribution: yup.number()
                .typeError('O valor deve ser um nmero')
                .required("Aporte  obrigatrio")
                .positive("O nmero deve ser positivo."),
            period: yup.number("Prazo deve ser um nmero")
                .typeError('O valor deve ser um nmero')
                .required('Prazo  obrigatrio')
                .positive("O nmero deve ser positivo."),
            monthlyContribution: yup.number()
                .typeError('O valor deve ser um nmero')
                .required('Aporte mensal  obrigatrio')
                .positive("O nmero deve ser positivo."),
            profitability: yup.number()
                .typeError('O valor deve ser um nmero')
                .required('Rentabilidade  obrigatrio')
                .positive("O nmero deve ser positivo."),
            ipca: yup.number()
                .typeError('O valor deve ser um nmero')
                .required(' obrigatrio')
                .positive("O nmero deve ser positivo."),
            cdi: yup.number()
                .typeError('O valor deve ser um nmero')
                .required(' obrigatrio')
                .positive("O nmero deve ser positivo.")
        }).required()
    )
};

export default simulationSchemaValidation;